	var pageoptions;
let _self;
export default {
	
	data() {
		return {
			username: '',
			password: ''
		};
	},
	methods: {
		onLoad:function(options){
			console.log('login_onload');
			console.log(options);
			pageoptions=options;
			console.log(pageoptions);
		},
		login() {
			const { username, password } = this;
			if (!username) {
				uni.showModal({
					content: '请填写用户名',
					showCancel: false
				});
				return;
			}
			if (!password) {
				uni.showModal({
					content: '请填写密码',
					showCancel: false
				});
				return;
			}
			uni.showLoading({
				title: '登录中...'
			});
			uni.request({
				url: 'https://www.auster.fun/doortp/public/api/member/login?token=api2020',
				method: 'POST',
				data: {
					user_name: username,
					user_pwd: password
				},
				success: res => {
					uni.hideLoading();
					console.log(res.data);
					if (res.data.code == 200) {
						uni.showToast({
							title: '登录成功',
							icon: 'none'
						});
						uni.setStorageSync('root_name', res.data.data.user_name);
						uni.setStorageSync('root_phone',res.data.data.user_phone);
						// 		uni.setStorageSync('username', res.result.username)
						if(pageoptions.backtype == 1){
							uni.redirectTo({url:pageoptions.backpage});
						}else{
							uni.switchTab({url:pageoptions.backpage});
						}
					}
					if(res.data.code == 400)
					{
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						});
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showModal({
						content:  '登录失败,请检查网络',
						showCancel: false
					})
				},
				complete: () => {}
			});

			// this.$cloud.callFunction({
			// 	name: 'login',
			// 	data: {
			// 		username,
			// 		password,
			// 	}
			// }).then((res) => {
			// 	uni.hideLoading()
			// 	if (res.result.token) {
			// 		uni.showToast({
			// 			title: '登录成功',
			// 			icon: 'none'
			// 		})
			// 		uni.setStorageSync('token', res.result.token)
			// 		uni.setStorageSync('username', res.result.username)
			// 		uni.switchTab({
			// 			url: '/pages/tabbar/member'
			// 		})
			// 	} else {
			// 		return Promise.reject(new Error(res.result.msg))
			// 	}
			// }).catch((err) => {
			// 	uni.hideLoading()
			// 	uni.showModal({
			// 		content: err.message || '登录失败',
			// 		showCancel: false
			// 	})
			// })
		}
	}
